<template>
	<view class="mianList2">
		<view class="mar"></view>
		<view class="modeloption">
			<view class="moption">{{xiangqing}}</view>
			<u-button class="submoption" shape="circle" size="mini" type="primary" :plain="true" @click="yemianchange"
				:ripple="true" ripple-bg-color="#B71C1C" plain>{{qiehuan}}</u-button>
		</view>
		<view class="partion2" v-show="this.yemiann === false">
			<view class="weixian">{{jiankongzhuangtai}}</view>
			<view class="xingwei">行为:{{action[0]}}</view>
			<view class="shipinjiankong">
				<video muted="true" @timeupdate="onViedoTimeUpdate" autoplay="true" loop="true" src=""
					class="viedoList2"></video>
			</view>
		</view>
		<view class="" v-if="this.yemiann === true">
			<view class="shauxin">刷新</view>
			<shishiwendu></shishiwendu>
			<zhuzhuangtu></zhuzhuangtu>
			<wendu></wendu>
		</view>
	</view>
</template>

<script>
	import zhuzhuangtu from '../../components/zhuzhuangtu.vue';
	import wendu from '../../components/wendu.vue';
	import shishiwendu from '../../components/shishiwendu.vue'
	export default {

		data() {
			return {
				action: ['正常', '肢体伸出区域外', '推车', '摔倒'],
				panduan: 1,
				yemiann: true,
				jiankongzhuangtai: '连接信号中',
				show: true,
				xiangqing: "扶梯数据",
				qiehuan: "监控",
			};
		},
		methods: {
			onViedoTimeUpdate(event) {},
			yemianchange() {
				this.yemiann = !this.yemiann
				if (!this.yemiann) {
					this.qiehuan = "数据"
					this.xiangqing = "监控中"
				} else {
					this.qiehuan = "监控"
					this.xiangqing = "扶梯数据"
				}
			}
		},
		components: {
			zhuzhuangtu,
			wendu,
			shishiwendu,
		}
	}
</script>

